import React, { Fragment } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import Home from "./components/Home";
import Gallery from "./components/Gallery";
import About from "./components/About";
import Services from "./components/Services";
import Contact from "./components/Contact";

const App = () => {
  return (
    <Router>
      <Fragment>
        <Navbar />
        <Switch>
          <Route exact path="/Yojkaj/" component={Home} />
          <Route exact path="/Yojkaj/gallery" component={Gallery} />
          <Route exact path="/Yojkaj/about" component={About} />
          <Route exact path="/Yojkaj/services" component={Services} />
          <Route exact path="/Yojkaj/contact" component={Contact} />
        </Switch>
        <Footer />
      </Fragment>
    </Router>
  );
};

export default App;
